import React, { Component } from "react";
import { logger } from "../../Helpers/Logger";
import Validator from "js-object-validation";
import {
   Button,
   Modal,
   ModalBody,
   ModalFooter,
   ModalHeader,
   Row,
   Col,
   FormGroup,
   Label,
   Input,
   Form,
   FormFeedback,
} from "reactstrap";
import {
   EditUserPaswordValidation,
   EditUserPaswordValidationMessages
} from "../../validations";
export class PasswordModal extends Component {
   constructor(props) {
      super(props);
      this.state = {
         password: "",
         confirmPassword: "",
         errors: {},
      }
   }
   componentDidMount() {
      this.setState({ error: {}, password: "", confirmPassword: "" })
   }
   componentDidUpdate({ userPasswordModalOpen }) {
      if (
         this.props.userPasswordModalOpen !== userPasswordModalOpen &&
         !this.props.userPasswordModalOpen
      ) {
         this.setState({
            password: "",
            confirmPassword: "",
            errors: {}
         });
      }
   }
   handleInputChange = e => {
      const { target } = e;
      const { name, value } = target;
      this.setState({
         [name]: value,
         errors: {
            ...this.state.errors,
            [name]: null
         }
      });
   };
   passwordUpdate = e => {
      e.preventDefault();
      try {
         const { password, confirmPassword } = this.state;
         const validData = {
            password: password.trim(),
            confirmPassword: confirmPassword.trim()
         };
         let { isValid, errors } = Validator(
            validData,
            EditUserPaswordValidation,
            EditUserPaswordValidationMessages
         );
         if (validData.password && !errors.password) {
            let res = (validData.password).match(/^(?=.*\d)(?=.*[a-zA-Z])[\w~@#$%^&*+=`|{}:;!.?()\]-]{6,20}$/);
            if (!res) {
               isValid = false;
               errors.password = "Password must have alphanumeric characters with optional (special characters)."
            }
         }
         if (!isValid) {
            this.setState({
               errors
            });
            return;
         } else {
            this.props.updatePassword(this.props.userId, password);
         }
      } catch (error) {
         logger(error);
      }
   };
   render() {
      const { userPasswordModalOpen, handlePasswordModal } = this.props;
      const { password, confirmPassword, errors } = this.state;
      return (
         <>
            <Row className="justify-content-center">
               <Col>
                  <Form onSubmit={this.passwordUpdate}>
                     <Modal
                        isOpen={userPasswordModalOpen}
                        toggle={handlePasswordModal}
                        backdrop={"static"}
                        className="customer-modal custom-form-modal"
                     >
                        <ModalHeader toggle={handlePasswordModal} >
                           {"Change user password"}
                        </ModalHeader>
                        <ModalBody >
                           <Row className="justify-content-center">
                              <Col lg={12}>
                                 <FormGroup>
                                    <Label
                                       htmlFor="name"
                                       className="customer-modal-text-style"
                                    >
                                       Password <span className={"asteric"}>*</span>
                                    </Label>
                                    <div className={"input-block"}>
                                       <Input
                                          type="password"
                                          name="password"
                                          value={password}
                                          onChange={this.handleInputChange}
                                          invalid={errors.password ? true : false}
                                          placeholder="Password"
                                       />
                                       <FormFeedback>
                                          {errors.password ? errors.password : null}
                                       </FormFeedback>
                                    </div>
                                 </FormGroup>

                                 <FormGroup>
                                    <Label
                                       htmlFor="name"
                                       className="customer-modal-text-style"
                                    >
                                       Confirm Password <span className={"asteric"}>*</span>
                                    </Label>
                                    <div className={"input-block"}>
                                       <Input
                                          type="password"
                                          name="confirmPassword"
                                          value={confirmPassword}
                                          onChange={this.handleInputChange}
                                          invalid={errors.confirmPassword ? true : false}
                                          placeholder="Confirm Password"
                                       />
                                       <FormFeedback>
                                          {errors.confirmPassword ? errors.confirmPassword : null}
                                       </FormFeedback>
                                    </div>
                                 </FormGroup>
                              </Col>
                           </Row>

                        </ModalBody>

                        <ModalFooter>
                           <div className="required-fields">*Fields are Required.</div>
                           <Button color="primary" onClick={this.passwordUpdate}>
                              Change Password
                            </Button>{" "}
                           <Button color="secondary" onClick={handlePasswordModal}>
                              Cancel
                            </Button>
                        </ModalFooter>
                     </Modal>

                  </Form>
               </Col>
            </Row>
         </>
      )
   }
}