import React, { Component } from "react";
import {
   Table,
   Badge,
   UncontrolledTooltip,
   Form,
   FormGroup,
   Row,
   Col,
   Label,
   InputGroup,
   Input,
   Button
} from "reactstrap";
import Loader from "../../containers/Loader/Loader";
import { formateDate } from "../../Helpers/Date";
import PaginationHelper from "../../Helpers/Pagination";
import { withRouter } from "react-router-dom";
import * as qs from "query-string";
import { AppConfig } from "../../Config/AppConfig";
import { ConfirmBox } from "../../Helpers/SweetAlert";
import { toast } from "react-toastify";
import NoDataFound from "../common/NoFound"

class UserList extends Component {
   constructor(props) {
      super(props);
      this.state = {
         page: 1,
         search: "",
         status: "",
         sort: "",
         user: {},
         openEditModal: false,
         selectedUsers: [],
         filterApplied: false,
         bulkAction: "",
         bulkAction1: false,
      };
   }
   componentDidMount() {
      const { location } = this.props;
      const lSearch = location.search;
      const { page, search, sort, status } = qs.parse(lSearch);
      let filterApplied = false;
      if (search || sort) {
         filterApplied = true;
      }
      this.setState({
         page: parseInt(page) || 1,
         sort: sort || "",
         status: status || "",
         search: search || "",
         filterApplied
      });
   }
   componentDidUpdate({ openEdit }) {
      if (this.props.openEdit !== openEdit) {
         this.setState({
            openEditModal: false
         });
      }
   }
   handleChange = e => {
      this.setState({
         [e.target.name]: e.target.value, bulkAction1: false
      });
   };
   onSearch = async (e) => {
      e.preventDefault();
      this.setState({
         page: 1,
         selectedUsers: []
      });
      const { search, sort, status, } = this.state;
      let param = {};
      param.page = 1;
      let hasFilter = false;
      if (search) {
         param.search = search;
         hasFilter = true;
      }
      if (sort) {
         param.sort = sort;
      }
      if (status) {
         param.status = status;
         hasFilter = true;
      }
      this.setState({ filterApplied: hasFilter });
      this.props.onSearch(param);
   };
   onReset = e => {
      e.preventDefault();
      this.setState({
         page: 1,
         search: "",
         status: "",
         sort: "",
         selectedUsers: [],
         filterApplied: false,
         bulkAction1: false
      });
      this.props.onSearch({});
   };
   onDelete = async (isMultiple = false) => {
      const { value } = await ConfirmBox({
         text: isMultiple
            ? "Do you want to delete selected user(s)?"
            : "Do you want to delete this user?"
      });
      if (!value) {
         this.setState({
            selectedUsers: []
         });
         return;
      }
      this.props.onDelete(this.state.selectedUsers);
      this.setState({
         selectedUsers: [],
         bulkAction: "",
         bulkAction1: false
      })
   };

   onProxy = () => {
      this.props.onProxy({ id: this.state.selectedUsers });
      this.setState({
         selectedUsers: [],
         bulkAction: "",
         bulkAction1: false
      })
   }
   editUser = user => {
      this.props.modelOperate({
         user,
         editUserModal: true
      });

   };
   onPassword = () => {
      this.props.onPassword({ id: this.state.selectedUsers });
      this.setState({
         selectedUsers: [],
         bulkAction: "",
         bulkAction1: false
      })
   };
   handleCheckboxChnage = e => {
      const { target } = e;
      const { checked, value } = target;
      const { selectedUsers } = this.state;
      if (checked) {
         selectedUsers.push(value);
         this.setState({
            selectedUsers
         });
         return;
      }
      const index = selectedUsers.indexOf(value);
      selectedUsers.splice(index, 1);
      this.setState({
         selectedUsers
      });
   };
   handleCheckAllCheckBox = e => {
      const { bulkAction1, selectedUsers } = this.state
      const { target } = e;
      if (bulkAction1) {
         this.setState({ bulkAction1: false })
      } else {
         this.setState({ bulkAction1: true })
      }
      const { checked } = target;
      if (!checked) {
         this.setState({
            selectedUsers: [],
            bulkAction: ""
         });
         return;
      }
      const { userData } = this.props;
      const { users } = userData;
      users.forEach(user => {
         selectedUsers.push(user._id);
      });
   };
   activateUsers = async (isMultiple = false) => {
      const { value } = await ConfirmBox({
         text: isMultiple
            ? "Do you want to active selected user(s)?"
            : "Do you want to active this user?"
      });
      if (!value) {
         this.setState({
            selectedUsers: [],
            bulkAction: "",
            bulkAction1: false
         });
         return;
      }
      this.props.onStatusUpdate({ status: 1, users: this.state.selectedUsers });
      this.setState({
         selectedUsers: [],
         bulkAction: "",
         bulkAction1: false
      })
   };
   deactivateUsers = async (isMultiple = false) => {
      const { value } = await ConfirmBox({
         text: isMultiple
            ? "Do you want to inactive selected user(s)?"
            : "Do you want to inactive this user?"
      });
      if (!value) {
         this.setState({
            selectedUsers: [],
            bulkAction: "",
            bulkAction1: false
         });
         return;
      }
      this.props.onStatusUpdate({ status: 0, users: this.state.selectedUsers });
      this.setState({
         selectedUsers: [],
         bulkAction: "",
         bulkAction1: false
      })
   };
   handleActionChange = e => {
      const { selectedUsers } = this.state;
      const { target } = e;
      const { value } = target;
      this.setState({
         bulkAction: value
      });
      if (!value) {
         return;
      }
      if (!selectedUsers.length) {
         toast.error("Please select at least one user.");
         this.setState({
            bulkAction: "",
            bulkAction1: false
         })
         return;
      }
      if (value === "active") {
         this.activateUsers(true);
      } else if (value === "inactive") {
         this.deactivateUsers(true);
      } else if (value === "delete") {
         this.onDelete(true);
      }
   };
   render() {
      const { userData, } = this.props;
      const { users, isLoading, totalUsers } = userData;
      const {
         page,
         search,
         sort,
         status,
         selectedUsers,
         filterApplied,
         bulkAction,
         bulkAction1
      } = this.state;
      return (
         <>
            <div className={"filter-block"}>
               <Form onSubmit={this.onSearch}>
                  <Row>
                     <Col lg={"3"} md={"3"} className="mb-0">
                        <FormGroup className="mb-0">
                           <InputGroup className="mb-2">
                              <input
                                 type="text"
                                 name="search"
                                 onChange={this.handleChange}
                                 className="form-control"
                                 value={search}
                                 aria-describedby="searchUser"
                                 placeholder="Search by user name and email"
                              />
                           </InputGroup>
                        </FormGroup>
                     </Col>
                     <Col lg={"2"} md={"2"} className="mb-0">
                        <FormGroup className="mb-0">
                           <Input
                              type="select"
                              name="status"
                              id="exampleSelect"
                              onChange={this.handleChange}
                              value={status}
                           >
                              <option className="form-control" value={""}>
                                 User Status
                              </option>
                              <option value={1}>Active</option>
                              <option value={0}>Inactive</option>
                           </Input>
                        </FormGroup>
                     </Col>
                     <Col lg={"2"} md={"2"} className="mb-0">
                        <FormGroup className="mb-0">
                           <Input
                              type="select"
                              name="sort"
                              id="SortFilter"
                              onChange={this.handleChange}
                              value={sort}
                           >
                              <option className="form-control" value={""}>
                                 Sort By
                              </option>
                              <option value={"createddesc"}>Sort by New</option>
                              <option value={"createdasc"}>Sort by Old</option>
                              <option value={"nasc"}>Sort by A-Z</option>
                              <option value={"ndesc"}>Sort by Z-A</option>
                           </Input>
                        </FormGroup>
                     </Col>
                     <Col lg={"3"} md={"3"} className="mb-0">
                        <Row>
                           <Col lg={"6"} md={"6"} className="mb-0">
                              <div className="filter-btn-wrap">
                                 <Label className="height17 label" />
                                 <div className="form-group mb-0">
                                    <span className="mr-2">
                                       <Button
                                          type="submit"
                                          className="btn btn-theme-transparent"
                                          id="Tooltip-1"
                                       >
                                          <i className="icons cui-magnifying-glass"></i>
                                       </Button>
                                       <UncontrolledTooltip target="Tooltip-1">
                                          Search
                                       </UncontrolledTooltip>
                                    </span>
                                    <span className="">
                                       <Button
                                          type="button"
                                          className="btn btn-theme-transparent"
                                          id="Tooltip-2"
                                          onClick={this.onReset}
                                       >
                                          <i className="icon-refresh icons"></i>
                                       </Button>
                                       <UncontrolledTooltip target={"Tooltip-2"}>
                                          Reset all filters
                                       </UncontrolledTooltip>
                                    </span>
                                 </div>
                              </div>
                           </Col>
                        </Row>
                     </Col>
                     <Col lg={"2"} md={"2"} className="mb-0 text-right">
                        <Button
                           type="button"
                           color="primary"
                           className="btn-theme"
                           onClick={
                              () => this.props.handleAddUser()
                           }
                        >
                           <i className="fa fa-plus" aria-hidden="true" />
                           &nbsp;Add User
                        </Button>
                     </Col>
                  </Row>
               </Form>
            </div>
            <Table responsive hover>
               <thead>
                  <tr>
                     <th style={{ maxWidth: "130px" }}>
                        <div className="table-checkbox-wrap">
                           <span className="checkboxli checkbox-custom checkbox-default">
                              <Input
                                 type="checkbox"
                                 name="checkbox"
                                 id="checkAll"
                                 disabled={!users.length}
                                 onChange={this.handleCheckAllCheckBox}
                                 checked={
                                    (selectedUsers.length === users.length && users.length) || bulkAction1
                                 }
                              />
                              <label className="" htmlFor="checkAll" />
                           </span>
                           <Input
                              className="commonstatus"
                              type="select"
                              id="exampleSelect"
                              onChange={this.handleActionChange}
                              disabled={!users.length}
                              value={bulkAction}
                           >
                              <option value={""}>Select</option>
                              {users.length ? (
                                 <>
                                    <option value={"active"}>Active</option>
                                    <option value={"inactive"}>Inactive</option>
                                 </>
                              ) : null}
                           </Input>
                        </div>
                     </th>
                     <th style={{ maxWidth: "180px" }}><i className={"fas fa-users"} /> User's Details</th>
                     <th style={{ maxWidth: "150px" }}><i className={"far fa-building"} /> Role-Type</th>
                     <th style={{ maxWidth: "90px" }} className={"text-center"}><i className={"fa fa-exclamation-circle"} /> Status</th>
                     <th style={{ maxWidth: "130px" }}><i className={"fas fa-clock"} /> Created /  Login Details</th>
                     <th style={{ maxWidth: "180px" }} className={"text-center"}>Action</th>
                  </tr>
               </thead>
               <tbody>
                  {!isLoading ? (
                     users.length ? (
                        users.map((user, index) => {
                           return (
                              <tr key={index}>
                                 <td style={{ maxWidth: "130px" }}>
                                    <div className="checkbox-custom checkbox-default coloum-checkbox">
                                       <Input
                                          type="checkbox"
                                          value={user._id}
                                          checked={selectedUsers.indexOf(user._id) > -1 || bulkAction1}
                                          name="checkbox"
                                          onChange={this.handleCheckboxChnage}
                                       />
                                       <label htmlFor={user._id}>
                                          {(page - 1) * AppConfig.ITEMS_PER_PAGE + index + 1}.
                                       </label>
                                    </div>
                                 </td>
                                 <td style={{ maxWidth: "180px" }}>
                                    <div className={"text-capitalize font-weight-semibold"}>
                                       {user.firstName ? [user.firstName, user.lastName].join(" ").trim() : <span className="padding">-</span>}
                                    </div>
                                    <div>{user.email ? <a href={`mailto:${user.email}`} className={"text-body"}>{user.email}</a> : <span className="padding">-</span>} </div>
                                 </td>
                                 <td style={{ maxWidth: "150px" }} className={"text-capitalize"}>
                                    {user.roleType ? user.roleType : <span>N/A</span>}
                                 </td>
                                 <td style={{ maxWidth: "90px" }} className={"text-center"}>
                                    {user.status ? (
                                       <>
                                          <Badge
                                             className={"badge-button"}
                                             color="success"
                                             id={`userStatus-${user._id}`}
                                             onClick={() => {
                                                this.setState(
                                                   {
                                                      selectedUsers: [user._id]
                                                   },
                                                   () => {
                                                      this.deactivateUsers();
                                                   }
                                                );
                                             }}
                                          >
                                             Active
                                          </Badge>
                                          <UncontrolledTooltip target={`userStatus-${user._id}`}>
                                             Inactivate {user.firstName}
                                          </UncontrolledTooltip>
                                       </>
                                    ) : (
                                          <>
                                             <Badge
                                                className={"badge-button"}
                                                color="danger"
                                                id={`userStatus1-${user._id}`}
                                                onClick={() => {
                                                   this.setState(
                                                      {
                                                         selectedUsers: [user._id]
                                                      },
                                                      () => {
                                                         this.activateUsers();
                                                      }
                                                   );
                                                }}
                                             >
                                                Inactive
                                             </Badge>
                                             <UncontrolledTooltip target={`userStatus1-${user._id}`}>
                                                Activate {user.firstName}
                                             </UncontrolledTooltip>
                                          </>
                                       )}
                                 </td>
                                 <td style={{ maxWidth: "130px" }}>
                                    <div id={`create${index}`}>
                                       {user.createdAt ? formateDate(user.createdAt) : <span>N/A</span>}
                                       {user.createdAt ?
                                          <UncontrolledTooltip target={`create${index}`}>
                                             Created At
                                          </UncontrolledTooltip> : null}
                                    </div>
                                    <div id={`loginip-${index}`}>
                                       {user.loggedInAt ? formateDate(user.loggedInAt) : <span>N/A</span>}
                                       {user.loggedInAt ?
                                          <UncontrolledTooltip target={`loginip-${index}`}>
                                             Last Login At
                                          </UncontrolledTooltip> : null}
                                    </div>
                                 </td>
                                 <td style={{ maxWidth: "180px" }} className={"text-center"}>
                                    <Button
                                       size={"sm"}
                                       onClick={() => this.editUser(user)}
                                       id={`edit-${user._id}`}
                                       className={"btn-theme-transparent"}
                                    >
                                       <i className={"icons cui-pencil"} />
                                    </Button>{" "}
                                    <UncontrolledTooltip target={`edit-${user._id}`}>
                                       Edit
                                    </UncontrolledTooltip>
                                    {" "}
                                    &nbsp;
                                    <Button
                                       size={"sm"}
                                       onClick={() =>
                                          this.setState(
                                             {
                                                selectedUsers: [user._id]
                                             },
                                             () => {
                                                this.onPassword();
                                             }
                                          )
                                       }
                                       id={`pass-${user._id}`}
                                       className={"btn-theme-transparent"}
                                    >
                                       <i className={"fa fa-key"} />
                                    </Button>
                                    <UncontrolledTooltip target={`pass-${user._id}`}>
                                       Change Password
                                    </UncontrolledTooltip>
                                    {" "}
                                    &nbsp;
                                    <Button
                                       size={"sm"}
                                       onClick={() =>
                                          this.setState(
                                             {
                                                selectedUsers: [user._id]
                                             },
                                             () => {
                                                this.onProxy();
                                             }
                                          )
                                       }
                                       id={`proxy-${user._id}`}
                                       className={"btn-theme-transparent"}
                                    >
                                       <i className={"fas fa-unlock-alt"} />
                                    </Button>
                                    <UncontrolledTooltip target={`proxy-${user._id}`}>
                                       Proxy Login
                                    </UncontrolledTooltip>
                                    {" "}
                                    &nbsp;
                                    <Button
                                       size={"sm"}
                                       onClick={() =>
                                          this.setState(
                                             {
                                                selectedUsers: [user._id]
                                             },
                                             () => {
                                                this.onDelete();
                                             }
                                          )
                                       }
                                       id={`delete-${user._id}`}
                                       className={"btn-theme-transparent"}
                                    >
                                       <i className={"fa fa-trash"} />
                                    </Button>
                                    <UncontrolledTooltip target={`delete-${user._id}`}>
                                       Delete
                                    </UncontrolledTooltip>
                                 </td>
                              </tr>
                           );
                        })
                     ) : (
                           <tr>
                              <td className={"text-center"} colSpan={12}>
                                 {filterApplied ? <NoDataFound message={"No User details found related to your search"} noResult /> :
                                    <NoDataFound message={"Currently there are no User details added."} />
                                 }
                              </td>
                           </tr>
                        )
                  ) : (
                        <tr>
                           <td className={"text-center loading"} colSpan={12}>
                              <Loader />
                           </td>
                        </tr>
                     )
                  }
               </tbody>
            </Table>
            {totalUsers && !isLoading ? (
               <PaginationHelper
                  totalRecords={totalUsers}
                  currentPage={page}
                  onPageChanged={page => {
                     this.setState({ page });
                     this.props.onPageChange(page);
                  }}
                  pageLimit={AppConfig.ITEMS_PER_PAGE}
               />
            ) : null}
         </>
      );
   }
}

export default withRouter(UserList);
