import React, { Component } from "react";
import {
   Button,
   Modal,
   ModalBody,
   ModalFooter,
   ModalHeader,
   Row,
   Col,
   FormGroup,
   Label,
   Input,
   Form,
   FormFeedback,
   InputGroup
} from "reactstrap";
import { logger } from "../../Helpers/Logger";
import Validator from "js-object-validation";
import {
   EditUserValidations,
   EditUserValidationsMessaages
} from "../../validations";
import LastUpdated from "../common/LastUpdated";

export class UserModal extends Component {
   constructor(props) {
      super(props);
      this.state = {
         firstName: "",
         lastName: "",
         email: "",
         roleType: "Unclassified",
         errors: {},
         webError: {}
      };
   }
   componentDidMount() {
      this.setState({ errors: {} })
   }

   componentDidUpdate({ userModalOpen, userData }) {
      if (
         this.props.userData &&
         this.props.userData._id &&
         (userData._id !== this.props.userData._id) && this.props.userModalOpen) {
         const {
            firstName,
            lastName,
            email,
            roleType,
         } = this.props.userData;
         this.setState({
            isEditMode: true,
            firstName: firstName || "",
            lastName: lastName || "",
            email: email || "",
            roleType: roleType || "",
            errors: {}
         });
      }
   }
   handleClick = e => {
      this.setState({
         permissions: {
            ...this.state.permissions,
            [e.target.name]: e.target.checked
         }
      });
   };
   handleInputChange = e => {
      const { target } = e;
      const { name, value } = target;
      this.setState({
         [name]: value,
         errors: {
            ...this.state.errors,
            [name]: null
         }
      });
   };
   editUser = e => {
      e.preventDefault();
      try {
         const {
            firstName,
            lastName,
            email,
            roleType
         } = this.state;
         const payload = {
            firstName: firstName.trim(),
            lastName: lastName.trim(),
            email: email.trim(),
            roleType: roleType.trim()
         };
         let { isValid, errors } = Validator(
            payload,
            EditUserValidations,
            EditUserValidationsMessaages
         );
         if (!isValid) {
            this.setState({
               errors
            });
            return;
         } else {
            this.props.updateUser(this.props.userData._id, payload);
         }
      } catch (error) {
         logger(error);
      }
   };
   render() {
      const { userModalOpen, handleUserModal, userData } = this.props;
      const {
         firstName,
         lastName,
         email,
         roleType,
         errors,
         isEditMode
      } = this.state;
      return (
         <>
            <Form onSubmit={this.editUser}>
               <Modal
                  isOpen={userModalOpen}
                  toggle={handleUserModal}
                  backdrop={"static"}
                  className="customer-modal custom-form-modal custom-modal-lg"
               >
                  <ModalHeader toggle={handleUserModal}>
                     {"Update user details"}
                     {isEditMode && userData && userData.updatedAt ? <LastUpdated updatedAt={userData.updatedAt} /> : null}
                  </ModalHeader>
                  <ModalBody>
                     <Row className="justify-content-center">
                        <Col md="6">
                           <FormGroup>
                              <InputGroup>
                                 <Label
                                    htmlFor="name"
                                    className="customer-modal-text-style"
                                 >
                                    First Name <span className={"asteric"}>*</span>
                                 </Label>
                                 <div className={"input-block"}>
                                    <Input
                                       type="text"
                                       placeholder="John"
                                       onChange={this.handleInputChange}
                                       value={firstName ? firstName : ""}
                                       name="firstName"
                                       invalid={errors.firstName ? true : false}
                                    />
                                    <FormFeedback>
                                       {errors.firstName ? errors.firstName : null}
                                    </FormFeedback>
                                 </div>
                              </InputGroup>
                           </FormGroup>
                        </Col>
                        <Col md="6">
                           <FormGroup>
                              <InputGroup>
                                 <Label
                                    htmlFor="name"
                                    className="customer-modal-text-style"
                                 >
                                    Last Name <span className={"asteric"}>*</span>
                                 </Label>
                                 <div className={"input-block"}>
                                    <Input
                                       type="text"
                                       placeholder="Doe"
                                       onChange={this.handleInputChange}
                                       value={lastName ? lastName : ""}
                                       name="lastName"
                                       invalid={errors.lastName ? true : false}
                                    />
                                    <FormFeedback>
                                       {errors.lastName ? errors.lastName : null}
                                    </FormFeedback>
                                 </div>
                              </InputGroup>
                           </FormGroup>
                        </Col>
                     </Row>
                     <Row className="justify-content-center">
                        <Col md="6">
                           <FormGroup>
                              <Label
                                 htmlFor="name"
                                 className="customer-modal-text-style"
                              >
                                 Email <span className={"asteric"}>*</span>
                              </Label>
                              <div className={"input-block"}>
                                 <Input
                                    type="text"
                                    placeholder="john.doe@example.com"
                                    onChange={this.handleInputChange}
                                    value={email ? email : ""}
                                    name="email"
                                    invalid={errors.email ? true : false}
                                 />
                                 <FormFeedback>
                                    {errors.email ? errors.email : null}
                                 </FormFeedback>
                              </div>
                           </FormGroup>
                        </Col>
                        <Col md="6">
                           <FormGroup>
                              <Label
                                 htmlFor="name"
                                 className="customer-modal-text-style"
                              >
                                 RoleType
                              </Label>
                              <Row className="">
                                 <Col md="6" >
                                    <Label check className="pr-5">
                                       <Input
                                          type="radio"
                                          name="roleType"
                                          value="teacher"
                                          onChange={this.handleInputChange}
                                          checked={roleType === "teacher"}
                                       />
                                       Teacher
                                    </Label>
                                 </Col>
                                 <Col md="6" >
                                    <Label check className="pr-5">
                                       <Input
                                          type="radio"
                                          name="roleType"
                                          value="student"
                                          onChange={this.handleInputChange}
                                          checked={roleType === "student"}
                                       />
                                       Student
                                    </Label>
                                 </Col>
                              </Row>
                           </FormGroup>
                        </Col>
                     </Row>
                  </ModalBody>
                  <ModalFooter>
                     <div className="required-fields">*Fields are Required.</div>
                     <Button color="primary" onClick={this.editUser}>
                        Update User
                     </Button>
                     {" "}
                     <Button color="secondary" onClick={handleUserModal}>
                        Cancel
                     </Button>
                  </ModalFooter>
               </Modal>
            </Form>
         </>
      );
   }
}
