import React, { Component } from "react";
import {
   Card,
   CardBody,
} from "reactstrap";
import { AddUserModal } from "../../components/common/AddUserModal";
import { UserModal } from "../../components/common/UserModal";
import { PasswordModal } from "../../components/common/PasswordModel";
import UsersList from "../../components/UsersList";
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import { AppRoutes } from "../../Config/AppRoutes";
import {
   getUsersList,
   deleteUser,
   editUser,
   proxyLoginRequest,
   updateUserStatus,
   modelOpenRequest,
   userPasswordUpdateRequest,
   addUserRequest,
   redirectTo
} from "../../actions";
import * as qs from "query-string";
import { isEqual } from "../../Helpers/Object";
class Users extends Component {
   constructor(props) {
      super(props);
      this.state = {
         openCreate: false,
         openEdit: false,
         userData: {},
         userId: ""
      };
   }
   componentDidMount() {
      const query = qs.parse(this.props.location.search);
      this.props.getUsers({ ...query, page: query.page || 1 });
   }
   onPageChange = page => {
      const { location } = this.props;
      const { search } = location;
      const query = qs.parse(search);
      this.props.onGoPage(`${AppRoutes.USERS.url}?${qs.stringify({ page: page, search: query.search, sort: query.sort, status: query.status })}`);
   };
   componentDidUpdate({ userReducer, location }) {
      if (
         this.props.userReducer.userData.isSuccess !==
         userReducer.userData.isSuccess
      ) {
         if (this.props.userReducer.userData.isSuccess) {
            const query = qs.parse(this.props.location.search);
            this.props.getUsers({ ...query, page: query.page || 1 });
         }
      }
      if (
         userReducer.userData.isEditSuccess !==
         this.props.userReducer.userData.isEditSuccess
      ) {
         if (this.props.userReducer.userData.isEditSuccess) {
            const query = qs.parse(this.props.location.search);
            this.props.getUsers({ ...query, page: query.page || 1 });
         }
      }
      const prevQuery = qs.parse(location.search);
      const currQuery = qs.parse(this.props.location.search);
      if (!isEqual(prevQuery, currQuery)) {
         this.props.getUsers({ ...currQuery, page: currQuery.page || 1 });
      }
   }
   toggleUpdatePassword = dataValue => {

      this.setState({
         userId: dataValue.id
      });
      const { modelDetails } = this.props.modelInfoReducer;
      let data = {
         updatePasswordModal: !modelDetails.updatePasswordModal,
         editUserModal: false,
         addUserModal: false
      };
      this.props.modelOperate(data);
   };
   toggleEditPassword = dataValue => {
      this.setState({
         userId: ""
      });
      const { modelDetails } = this.props.modelInfoReducer;
      let data = {
         updatePasswordModal: !modelDetails.updatePasswordModal,
         editUserModal: false,
         addUserModal: false
      };
      this.props.modelOperate(data);
   };

   toggleUpdateUser = dataValue => {
      this.setState({
         userData: dataValue.user,
      });
      const { modelDetails } = this.props.modelInfoReducer;
      let data = {
         editUserModal: !modelDetails.editUserModal,
         updatePasswordModal: false,
         addUserModal: false
      };
      this.props.modelOperate(data);
   };
   toggleEditUser = dataValue => {
      this.setState({
         userData: {}
      });
      const { modelDetails } = this.props.modelInfoReducer;
      let data = {
         editUserModal: !modelDetails.editUserModal,
         updatePasswordModal: false,
         addUserModal: false
      };
      this.props.modelOperate(data);
   };
   toggleAddUser = () => {
      const { modelDetails } = this.props.modelInfoReducer;
      let data = {
         addUserModal: !modelDetails.addUserModal,
         updatePasswordModal: false,
         editUserModal: false
      };
      this.props.modelOperate(data);
   }
   toggleAddUserClose = () => {
      // const { modelDetails } = this.props.modelInfoReducer;
      let data = {
         addUserModal: false,
         updatePasswordModal: false,
         editUserModal: false
      };
      this.props.modelOperate(data);
   }
   onSearch = data => {
      this.props.onGoPage(`${AppRoutes.USERS.url}?${qs.stringify({ page: data.page, search: data.search, sort: data.sort, status: data.status })}`);
   };
   deleteUser = userId => {
      const { location } = this.props;
      const { search } = location;
      const query = qs.parse(search);
      this.props.deleteUser({ ...query, userId });
   };
   onStatusUpdate = data => {
      const { location } = this.props;
      const { search } = location;
      const query = qs.parse(search);
      this.props.onStatusUpdate({ ...query, ...data });
   };
   updateUser = (id, data) => {
      this.props.updateUser(id, data);

   }
   updatePassword = (id, data) => {

      this.props.updatePassword(id, data);
   }
   onProxyLogin = data => {
      this.props.onProxyLogin(data);
   }
   addUserRequest = data => {
      const { location } = this.props;
      const { search } = location;
      const query = qs.parse(search);
      this.props.addUserRequest({ data: data, ...query });
   }
   render() {
      const { userReducer, modelInfoReducer, } = this.props;
      const { modelDetails } = modelInfoReducer;
      const { editUserModal } = modelDetails;
      return (
         <>
            <Helmet>
               <title>Superadmin/Users</title>
            </Helmet>
            <Card className={"white-card"}>
               <CardBody className={"custom-card-body position-relative"}>
                  <UsersList
                     userData={userReducer}
                     onPageChange={this.onPageChange}
                     onSearch={this.onSearch}
                     onDelete={this.deleteUser}
                     onUpdate={this.props.toggleUpdateUser}
                     onPassword={this.toggleUpdatePassword}
                     openEdit={editUserModal}
                     onProxy={this.onProxyLogin}
                     onStatusUpdate={this.onStatusUpdate}
                     modelOperate={this.toggleUpdateUser}
                     handleAddUser={this.toggleAddUser}
                  />
               </CardBody>
            </Card>
            <UserModal
               userModalOpen={modelDetails.editUserModal}
               handleUserModal={this.toggleEditUser}
               updateUser={this.updateUser}
               userData={this.state.userData}
            />
            <PasswordModal
               userPasswordModalOpen={modelDetails.updatePasswordModal}
               updatePassword={this.updatePassword}
               handlePasswordModal={this.toggleEditPassword}
               userId={this.state.userId}
            />
            <AddUserModal
               addUserModalOpen={modelDetails.addUserModal}
               handleAddUserModal={this.toggleAddUserClose}
               addUser={this.addUserRequest}
            />

         </>
      );
   }
}
const mapStateToProps = state => ({
   userReducer: state.usersReducer,
   modelInfoReducer: state.modelInfoReducer
});

const mapDispatchToProps = dispatch => ({
   modelOperate: data => {
      dispatch(modelOpenRequest({ modelDetails: data }));
   },
   getUsers: data => {
      dispatch(getUsersList(data));
   },
   updateUser: (id, data) => {
      dispatch(editUser({ id, data }));
   },
   updatePassword: (id, data) => {
      dispatch(userPasswordUpdateRequest({ id, data }))
   },
   deleteUser: data => {
      dispatch(deleteUser(data));
   },
   onStatusUpdate: data => {
      dispatch(updateUserStatus(data));
   },
   onProxyLogin: data => {
      dispatch(proxyLoginRequest(data));
   },
   addUserRequest: data => dispatch(addUserRequest(data)),
   onGoPage: data => {
      dispatch(redirectTo({ path: data }));
   }
});

export default connect(
   mapStateToProps,
   mapDispatchToProps
)(Users);
