import React, { Component } from "react";
import {
   Button,
   Modal,
   ModalBody,
   ModalFooter,
   ModalHeader,
   Row,
   Col,
   FormGroup,
   Label,
   Input,
   Form,
   FormFeedback,
   InputGroup
} from "reactstrap";
import { logger } from "../../Helpers/Logger";
import Validator from "js-object-validation";
import {
   AddUserValidations,
   AddUserValidationsMessaages
} from "../../validations";

export class AddUserModal extends Component {
   constructor(props) {
      super(props);
      this.state = {
         firstName: "",
         lastName: "",
         email: "",
         roleType: "",
         roleTypeChecked: false,
         password: "",
         confirmPassword: "",
         errors: {},
      };
   }
   componentDidMount() {
      this.setState({ errors: {} })
   }

   componentDidUpdate({ addUserModalOpen }) {
      if (addUserModalOpen !== this.props.addUserModalOpen) {
         this.removeState();
      }
   }
   removeState = () => {
      this.setState({
         firstName: "",
         lastName: "",
         email: "",
         roleType: "",
         password: "",
         confirmPassword: "",
         errors: {},
      })
   }
   handleInputChange = e => {
      const { target } = e;
      const { name, value } = target;
      this.setState({
         [name]: value,
         errors: {
            ...this.state.errors,
            [name]: null
         }
      });
   };
   handleChange = e => {
      const { target } = e;
      const { name, checked } = target;
      this.setState({
         [name]: checked,
         errors: {
            ...this.state.errors,
            [name]: null
         }
      })
      if (name === "roleTypeChecked" && checked === true) {
         this.setState({
            roleType: "teacher"
         })
      } else {
         this.setState({
            roleType: "Unclassified"
         })
      }
   }
   addUser = e => {
      e.preventDefault();
      try {
         const {
            firstName,
            lastName,
            email,
            roleType,
            password,
            confirmPassword
         } = this.state;
         const payload = {
            firstName: firstName.trim(),
            lastName: lastName.trim(),
            email: email.trim(),
            roleType: roleType.trim(),
            password: password.trim(),
            confirmPassword: confirmPassword.trim()
         };
         let { isValid, errors } = Validator(
            payload,
            AddUserValidations,
            AddUserValidationsMessaages
         );
         if (payload.password && !errors.password) {
            let res = (payload.password).match(/^(?=.*\d)(?=.*[a-zA-Z])[\w~@#$%^&*+=`|{}:;!.?()\]-]{6,20}$/);
            if (!res) {
               isValid = false;
               errors.password = "Password must have alphanumeric characters with optional (special characters)."
            }
         }
         if (!isValid) {
            this.setState({
               errors
            });
            return;
         } else {
            this.props.addUser(payload);
         }
      } catch (error) {
         logger(error);
      }
   };
   render() {
      const { addUserModalOpen, handleAddUserModal } = this.props;
      const {
         firstName,
         lastName,
         email,
         roleTypeChecked,
         password,
         confirmPassword,
         errors,
      } = this.state;
      return (
         <>
            <Form onSubmit={this.addUser}>
               <Modal
                  isOpen={addUserModalOpen}
                  toggle={handleAddUserModal}
                  backdrop={"static"}
                  className="customer-modal custom-form-modal custom-modal-lg"
               >
                  <ModalHeader toggle={handleAddUserModal}>
                     Add User
                  </ModalHeader>
                  <ModalBody>
                     <Row className="justify-content-center">
                        <Col md="6">
                           <FormGroup>
                              <InputGroup>
                                 <Label
                                    htmlFor="name"
                                    className="customer-modal-text-style"
                                 >
                                    First Name <span className={"asteric"}>*</span>
                                 </Label>
                                 <div className={"input-block"}>
                                    <Input
                                       type="text"
                                       placeholder="John"
                                       onChange={this.handleInputChange}
                                       value={firstName ? firstName : ""}
                                       name="firstName"
                                       invalid={errors.firstName ? true : false}
                                    />
                                    <FormFeedback>
                                       {errors.firstName ? errors.firstName : null}
                                    </FormFeedback>
                                 </div>
                              </InputGroup>
                           </FormGroup>
                        </Col>
                        <Col md="6">
                           <FormGroup>
                              <InputGroup>
                                 <Label
                                    htmlFor="name"
                                    className="customer-modal-text-style"
                                 >
                                    Last Name <span className={"asteric"}>*</span>
                                 </Label>
                                 <div className={"input-block"}>
                                    <Input
                                       type="text"
                                       placeholder="Doe"
                                       onChange={this.handleInputChange}
                                       value={lastName ? lastName : ""}
                                       name="lastName"
                                       invalid={errors.lastName ? true : false}
                                    />
                                    <FormFeedback>
                                       {errors.lastName ? errors.lastName : null}
                                    </FormFeedback>
                                 </div>
                              </InputGroup>
                           </FormGroup>
                        </Col>
                     </Row>
                     <Row className="justify-content-center">
                        <Col md="6">
                           <FormGroup>
                              <Label
                                 htmlFor="name"
                                 className="customer-modal-text-style"
                              >
                                 Email <span className={"asteric"}>*</span>
                              </Label>
                              <div className={"input-block"}>
                                 <Input
                                    type="text"
                                    placeholder="john.doe@example.com"
                                    onChange={this.handleInputChange}
                                    value={email ? email : ""}
                                    name="email"
                                    invalid={errors.email ? true : false}
                                 />
                                 <FormFeedback>
                                    {errors.email ? errors.email : null}
                                 </FormFeedback>
                              </div>
                           </FormGroup>
                        </Col>
                        <Col md="6">
                           <FormGroup>
                              <Label
                                 htmlFor="name"
                                 className="customer-modal-text-style"
                              >
                                 Password <span className={"asteric"}>*</span>
                              </Label>
                              <div className={"input-block"}>
                                 <Input
                                    type="password"
                                    onChange={this.handleInputChange}
                                    value={password ? password : ""}
                                    name="password"
                                    invalid={errors.password ? true : false} />
                                 <FormFeedback>
                                    {errors.password ? errors.password : null}
                                 </FormFeedback>
                              </div>
                           </FormGroup>
                        </Col>
                     </Row>
                     <Row>
                        <Col md="6">
                           <FormGroup>
                              <Label
                                 htmlFor="name"
                                 className="customer-modal-text-style"
                              >
                                 RoleType
                              </Label>
                              <div className={"input-block custom-control custom-control-alternative custom-checkbox mb-4"}>
                                 <Input
                                    className="custom-control-input"
                                    id="customCheckRegister"
                                    value={roleTypeChecked}
                                    name={"roleTypeChecked"}
                                    onChange={this.handleChange}
                                    type="checkbox"
                                 />
                                 <label
                                    className="custom-control-label cursor_pointer"
                                    htmlFor="customCheckRegister"
                                 >
                                    <span className="float-left">I am a Teacher</span>
                                 </label>
                              </div>
                           </FormGroup>
                        </Col>
                        <Col md="6">
                           <FormGroup>
                              <Label
                                 htmlFor="name"
                                 className="customer-modal-text-style"
                              >
                                 Confirm Password <span className={"asteric"}>*</span>
                              </Label>
                              <div className={"input-block"}>
                                 <Input
                                    type="password"
                                    onChange={this.handleInputChange}
                                    value={confirmPassword ? confirmPassword : ""}
                                    name="confirmPassword"
                                    invalid={errors.confirmPassword ? true : false} />
                                 <FormFeedback>
                                    {errors.confirmPassword ? errors.confirmPassword : null}
                                 </FormFeedback>
                              </div>
                           </FormGroup>
                        </Col>
                     </Row>
                  </ModalBody>
                  <ModalFooter>
                     <div className="required-fields">*Fields are Required.</div>
                     <Button color="primary" onClick={this.addUser}>
                        Add User
                     </Button>
                     {" "}
                     <Button color="secondary" onClick={handleAddUserModal}>
                        Cancel
                     </Button>
                  </ModalFooter>
               </Modal>
            </Form>
         </>
      );
   }
}